const createProfileValidations = values => {
  const errors = {};

  if (!values.username) {
    errors.username = ' ';
  }

  if (!values.weight) {
    errors.weight = ' ';
  }

  if (!values.caloriesPerDay) {
    errors.caloriesPerDay = ' ';
  }

  if (!values.age) {
    errors.age = ' ';
  }

  return errors;
}

export default createProfileValidations;
