import React from 'react';
import { connect } from 'react-redux';

class AverageStats extends React.Component {
  render() {
    if (!this.props.stats) {
      return null;
    }

    return (
      <div className="ui secondary pointing menu">
        <span className="item">
           : <b className="pl-5">{this.props.stats.averageWeight}  </b>
        </span>
        <span className="item">
            /: <b className="pl-5">{this.props.stats.averageCaloriesPerDay} </b>
        </span>
        <span className="item">
            ./: <b className="pl-5">{this.props.stats.averageActivityPerDay} </b>
        </span>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    stats: state.user.profile ? state.user.profile.stats : null,
  };
}

const mapDispatchToProps = {

};

export default connect(mapStateToProps, mapDispatchToProps)(AverageStats);
